body {
  font-family: "Roboto", sans-serif, "Roboto", sans-serif;
  color: #000000;
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none;
  list-style-type: none;
}

p {
  padding: 0;
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: inherit;
}

a:visited {
  color: inherit;
}

.container {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }
}
@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media screen and (min-width: 1024px) {
  .container {
    max-width: 1024px;
    padding-left: 71px;
    padding-right: 71px;
  }
}
header {
  background-color: black;
}

.header__container {
  padding-top: 45px;
  padding-bottom: 45px;
  width: 320px;
  height: 230px;
  background-image: url(../images/backgrounds/background320.png);
  background-color: black;
  color: #FFFFFF;
}
.header__container--myLibrary {
  background-image: url(../images/backgrounds/background320.png);
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.logo__title {
  display: none;
  font-weight: 500;
  font-size: 30px;
  line-height: 36px;
  align-self: center;
}
.logo__icon {
  width: 24px;
  height: 24px;
  stroke: #818181;
}

.nav {
  display: flex;
  align-items: start;
  justify-content: center;
}
.nav__item {
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  transition: color 500ms cubic-bezier(0.4, 0, 0.2, 1);
}
.nav__item:not(:first-child) {
  margin-left: 40px;
}
.nav__item:hover,
.nav__item :focus {
  color: #FF6B08;
}

.search__form {
  display: flex;
  margin: 40px auto auto;
  justify-content: center;
}
.search__input {
  position: relative;
  color: #FFFFFF;
  background-color: rgba(0, 0, 0, 0);
  width: 280px;
  border: none;
  border-bottom: 0.1px solid #FFFFFF;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  outline: none;
}
.search__input::placeholder {
  color: #FFFFFF;
}
.search__button {
  left: 260px;
  bottom: 20px;
  position: relative;
  right: 50px;
  background-color: transparent;
  border: none;
}
.search__icon {
  width: 12px;
  height: 12px;
  stroke: #FFFFFF;
  transition: stroke 500ms cubic-bezier(0.4, 0, 0.2, 1);
}
.search__icon :hover {
  stroke: #FF6B08;
  cursor: pointer;
}

.active::after {
  display: block;
  content: "";
  width: inherit;
  height: 3px;
  background-color: #FF6B08;
}

@media screen and (min-width: 1024px) {
  .header__container {
    width: auto;
    max-width: 1024px;
    background-image: url(../images/backgrounds/background1024.png);
  }
  .header__container--myLibrary {
    background-image: url(../images/backgrounds/background1024myLibrary.png);
  }
  .logo__title {
    display: inline-block;
  }
  .search__input {
    width: 340px;
  }
  .search__button {
    left: -20px;
    bottom: 0px;
  }
}
.is-hidden {
  opacity: 0;
  visibility: visible;
  pointer-events: none;
}

.modal {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translate(-50%, 0%);
  border: 1px #000000 solid;
  background-color: #fff;
  margin: 0 auto;
  max-width: 280px;
  padding: 48px 20px 40px;
  opacity: 1;
  z-index: 2;
}
.modal__close-btn {
  width: 12px;
  height: 12px;
}
.modal__close-btn--icon {
  width: 12px;
  height: 12px;
}
.modal__head {
  margin-top: 30px;
}
.modal__img {
  min-width: 240px;
  min-height: 350px;
  border: 1px solid grey;
}
.modal__img--picture {
  display: block;
}
.modal__list {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}
.modal__item {
  font-size: 12px;
  line-height: 16px;
  display: flex;
  margin-bottom: 10px;
  justify-content: space-between;
}
.modal__parameters {
  color: #8c8c8c;
  font-weight: 500px;
  font-size: 12px;
  line-height: 16px;
}
.modal__about {
  margin: 20px auto 10px 0px;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
}
.modal__text {
  font-weight: 500;
  font-size: 12px;
  line-height: 20px;
}
.modal__button-container {
  margin-top: 21px;
  display: flex;
  justify-content: space-between;
}
.modal__buttons {
  background-color: transparent;
  width: 112px;
  height: 44px;
  border: 1px solid #000000;
  border-radius: 5px;
  transition: color 500ms cubic-bezier(0.4, 0, 0.2, 1), background-color 500ms cubic-bezier(0.4, 0, 0.2, 1), border-color 500ms cubic-bezier(0.4, 0, 0.2, 1);
}
.modal__buttons:hover {
  color: #fff;
  border-color: #FF6B08;
  background-color: #FF6B08;
}
.modal__rating {
  display: inline-block;
  text-align: center;
  color: #fff;
  width: 36px;
  height: 16px;
  background-color: #FF6B08;
  border-radius: 5px;
}
.modal__votes-number {
  display: inline-block;
  text-align: center;
  color: black;
  width: 36px;
  height: 16px;
  background-color: #f7f7f7;
  border-radius: 5px;
}

.footer__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  padding: 28px;
  background-color: #f7f7f7;
}

@media screen and (min-width: 768px) {
  .footer__container {
    flex-direction: row;
    justify-content: center;
    height: 77px;
  }
}
.myLibrary__wrapper {
  margin-top: 46px;
  display: flex;
  justify-content: space-between;
}
.myLibrary__button {
  color: #FFFFFF;
  background-color: transparent;
  width: 136px;
  height: 44px;
  border-radius: 5px;
  border-color: white;
  border-style: solid;
  cursor: pointer;
}
.myLibrary__button:hover {
  border-color: #FF6B08;
}

@media screen and (min-width: 768px) {
  .myLibrary__wrapper {
    justify-content: center;
  }
  .myLibrary__button:first-child {
    margin-right: 15px;
  }
}